.back {
    position: absolute;
    right: 8%;
    top: -70px;
}

.single-page {
    position: relative;
}

.main-content p {
    margin: 10px 0 20px 0;
    color: grey;
}

.main-content .para p {
    margin-right: 10px;
}

.main-content h1 {
    font-size: 30px;
}

.main-content img {
    width: 100%;
    height: auto;
}

article {
    display: flex;
}

article .box {
    background-color: #31b675;
    padding: 30px;
    color: white;
}

article .box h2 {
    font-size: 30px;
    font-weight: 500;
}

article .box2 {
    margin-top: 40px;
    background: url("../../public/images/side-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    position: relative;
    padding: 20px;
}

article .box2::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
}

article p {
    font-size: 14px;
    line-height: 25px;
}

article .box2 p {
    text-align: center;
    position: relative;
    z-index: 2;
    height: 100%;
    font-size: 14px;
    line-height: 25px;
    color: white;
}

.single-page .outline-btn svg {
    font-size: 20px !important;
}

@media screen and (max-width: 900px) {
    .main-content {
        width: 100%;
    }

    .single-page .side-content {
        width: 100%;
        margin: 0;
    }

    article .box {
        padding: 15px;
    }

    .single-page .flex {
        flex-direction: column;
        align-items: center;
    }
}